<template>
  <div class="container">
    <el-tooltip  effect="dark" content="" placement="bottom">
      <i class="el-icon-lock icon" @click="handleClick"></i>
    </el-tooltip>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="close"
       >
      <el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="110px">
       <el-form-item label="" prop="value">
         <el-input clearable type="password" v-model="ruleForm.value" placeholder=""></el-input>
       </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="sure"> </el-button>
  </span>
    </el-dialog>
  </div>

</template>

<script>
  export default {
    name: "Lock",
    components: {},
    props: {},
    data() {
      return {
        dialogVisible: false,
        ruleForm: {
          value: ''
        },
        rules: {
          value: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 15, message: '6-15', trigger: 'blur'},
          ]
        }
      }
    },
    methods: {
      handleClick() {
        this.dialogVisible = true
      },
      close() {
        this.dialogVisible = false
        this.$refs.ruleForm.resetFields();
      },
      sure() {
        this.$refs.ruleForm.validate(valid => {
          if (valid) {
            sessionStorage.setItem('lockPwd', this.ruleForm.value)
            sessionStorage.setItem('isLock', 0)
            this.$router.push('/lockView')
            this.dialogVisible = false

          } else {
            this.$message.error(',')
          }
        })


      }

    },
    mounted() {

    },
    created() {

    },
    filters: {},
    computed: {},
    watch: {},
    directives: {}
  }
</script>

<style scoped lang="scss">
  .container {
    padding: 20px;
    .icon {
      font-size: 22px;
    }
  }
</style>
