<template>
  <div class="container">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item prop="phone">
        <el-input v-model="ruleForm.phone" placeholder="">
          <i slot="prefix" class="el-input__icon el-icon-phone"></i>
        </el-input>
      </el-form-item>
      <el-form-item prop="code">
        <el-row :gutter="20">
          <el-col :span="16">
            <el-input placeholder="" v-model="ruleForm.code">
              <i slot="prefix" class="el-input__icon el-icon-tickets"></i>
            </el-input>
          </el-col>
          <el-col :span="6" >
            <el-button @click="send"></el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width: 100%" @click="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
  export default {
    name: "PhoneLogin",
    components: {},
    props: {
      ruleForm: {
        type: Object,
        required: true
      },
    },
    data() {
      let checkPhone = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        } else {
          const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
          if (reg.test(value)) {
            callback();
          } else {
            return callback(new Error(''));
          }
        }
      }
      return {
        rules: {
          phone: [
            {validator: checkPhone, trigger: 'change'},
          ],
          code: [
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    methods: {
      submit(e) {
        this.$refs.ruleForm.validate(valid => {
          if (valid) {
            this.$emit('submit', e)
          } else {
            this.$emit('errHandle', e)
          }
        })
      },
      send(e) {
        this.$emit('send', e)
      }
    },
    mounted() {

    },
    created() {

    }
    ,
    filters: {}
    ,
    computed: {}
    ,
    watch: {}
    ,
    directives: {}
  }
</script>

